import axios from "axios";
import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import HeaderOtherPages from "./HeaderOtherPages";
import codingguy from "./resources/coding-g03bcc4bcb_1280.jpg";
import uploadHeaderImage from "./resources/mainBanner.jpg";
function AllBlogs() {
  useEffect(() => {
    axios
      .get("https://api.swipeupassignments.com/blogs")
      .catch((error) => {
        console.log(error);
        // setBlogs(error);
      })
      .then((response) => {
        setBlogs(response.data.data);
        // console.log(response.data.data);
      });
  }, []);
  const [getBlogs, setBlogs] = useState([
    {
      id: 1,
      title: "Blog 1",
      description:
        "A small river named Duden flows by their place and supplies it with the necessary regelialia.",
      date: "30-12-2022",
      thumbnail: codingguy,
    },
  ]);
  return (
    <>
      <HeaderOtherPages headerImage={uploadHeaderImage} title="Our Blogs" />
      <div className="AllBlogs">
        <div className="blogsContainer">
          {getBlogs.map((blog) => (
            <Link key={blog.id} className="Link" to="/blogs/">
              {" "}
              <div className="blogsList">
                <div
                  className="blogThumbnailBG"
                  style={{
                    backgroundImage: `url(${blog.thumbnail})`,
                  }}
                ></div>
                {/* <img src={blog.thumbnail} alt="blogImg" /> */}
                <h3>{blog.title}</h3>
                <p> {blog.summary} </p>
                <div className="blogInfo">
                  <p>{new Date(blog.createdAt).toDateString()}</p>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </>
  );
}
export default AllBlogs;
